import AWFRERecommendationsRequest from '../awfre/model/recommendations/awfre_recommendations_request';
import AWFRERecommendationsAdditionalDataRequest from './model/recommendations/awfre_recommendations_additional_data_request';
import AWFREDemographics from '../awfre/model/recommendations/awfre_demographics';

/**
 * This is the object factory for the American Well FRE SDK module <br>
 * 
 * @since 2.4.0
 */
class AWFREFactory {

  /**
   * @return new {model.recommendations.AWFRERecommendationsRequest}
   */
  get newRecommendationsRequest() {
    return new AWFRERecommendationsRequest();
  }

  /**
   * @return new {model.recommendations.AWFRERecommendationsAdditionalDataRequest}
   */
  get newRecommendationsAdditionalDataRequest() {
    return new AWFRERecommendationsAdditionalDataRequest();
  }

  /**
   * @return new {model.recommendations.AWFREDemographics}
   */
  get newAWFREDemographics() {
    return new AWFREDemographics();
  }
}

export default AWFREFactory;
