import GenericParser from '../../internal/parser/generic_parser';
import AWSDKVisitReport from './awsdk_visit_report';
import AWSDKResponse from '../../internal/model/response/awsdk_response';

/**
 * A wrapper around a collection of {@link AWSDKVisitReport}s
 * @property {Array} visitReports a collection of {@link AWSDKVisitReport}s
 * @extends model.AWSDKResponse
 * @deprecated see {@link model.AWSDKPaginatedVisitReports}
 */
class AWSDKVisitReportList extends AWSDKResponse {
  get visitReports() {
    return this.__data.visitReports.map(item => GenericParser.parseObject(item, AWSDKVisitReport));
  }
}

export default AWSDKVisitReportList;
