/**
 * Enum for AWFREErrorCode.
 * @readonly
 * @enum
 * 
 */
const AWFREErrorCode = {
  /**
   * A generic error.
   * @since 2.4.0
   */
  otherError: 'otherError',

  /**
   * A client attempted to initialize FRE server with missing values for configuration.
   * @since 2.4.0
   */
  initializationError: 'initializationError',
  
    /**
   * The FRE platform is incompatible with this version of the sdk
   * @since 2.4.0
   */
  versionIncompatible: 'versionIncompatible',
};

/**
 * @private
 */
AWFREErrorCode.__codes = Object.values(AWFREErrorCode);

export default AWFREErrorCode;
