/**
 * The base class for all data objects in AWFRE.
 * 
 * @class
 * @ignore
 * 
 * @since 2.4.0
 */
class AWFREDataObject {
  constructor(data = {}) {
    this.__data = data;
  }

  toString() {
    const dataString = JSON.stringify(this.__data);
    return dataString;
  }
}

export default AWFREDataObject;
