/**
 * Request object holds additional recommendations data in order to fetch recommendations response from the AmWell platform.
 * 
 * @property {String} trackingId Unique identifier of the response we need to retrieve.
 *
 * @since 2.4.0
 */
class AWFRERecommendationsAdditionalDataRequest {
  get trackingId() {
    return this.__trackingId;
  }

  set trackingId(trackingId) {
    this.__trackingId = trackingId;
  }

  /**
   * @private
   */
  __getAsRequestBody() {
    const res = {};
    res.trackingId = this.__trackingId;
    return JSON.stringify(res);
  }
}
export default AWFRERecommendationsAdditionalDataRequest;