import AWFREDataObject from './awfre_dataobject';

/**
 * Service object that holds information about the enterprise's Service.
 * 
 * @property {String} id The service id.
 * @property {String} name The service name.
 * 
 * @since 2.4.0
 */
class AWFREService extends AWFREDataObject {
  get id() {
    return this.__data.id;
  }

  get name() {
    return this.__data.name;
  }
}

export default AWFREService;
