import AWSDKResponse from '../../internal/model/response/awsdk_response';
import GenericParser from '../../internal/parser/generic_parser';
import AWSDKHealthDocumentRecord from './awsdk_health_document_record';

/**
 * This class is a wrapper around a collection (Array) of objects of type {@link model.AWSDKHealthDocumentRecord|AWSDKHealthDocumentRecord}
 * @property {model.AWSDKHealthDocumentRecord[]} documentRecords an Array of {@link model.AWSDKHealthDocumentRecord|AWSDKHealthDocumentRecord} objects.
 * @extends model.AWSDKResponse
 * @deprecated see {@link model.AWSDKPaginatedHealthDocumentRecords}
 */
class AWSDKHealthDocumentRecordList extends AWSDKResponse {
  get documentRecords() {
    return this.__data.documentRecords.map(item => GenericParser.parseObject(item, AWSDKHealthDocumentRecord));
  }
}
export default AWSDKHealthDocumentRecordList;
