import AWFREDataObject from '../awfre_dataobject';
import AWFREVisitCost from '../../model/recommendations/awfre_visit_cost';
import GenericParser from '../../../awsdk/internal/parser/generic_parser';

/**
 * Practice object holds information of enterprise's practice.
 * 
 * @property {String} practiceName is the Practice name.
 * @property {String} practiceSourceId Unique identifier of the practice.
 * @property {String} practiceLogoUrl The URL for practice logo.
 * @property {String} practiceSmallLogoUrl The URL for practice small logo.
 * @property {model.AWFREVisitCost} visitCost The visit cost details.
 *
 * @since 2.4.0
 */
class AWFREPractice extends AWFREDataObject {
  get practiceName() {
    return this.__data.practiceName;
  }

  get practiceSourceId() {
    return this.__data.practiceSourceId;
  }

  get practiceLogoUrl() {
    return this.__data.practiceLogoUrl;
  }

  get practiceSmallLogoUrl() {
    return this.__data.practiceSmallLogoUrl;
  }

  get visitCost() {
    return GenericParser.parseObject(this.__data.visitCost, AWFREVisitCost);
  }
}

export default AWFREPractice;
