import Service from '../../awsdk/service/service';
import Validator from '../../awsdk/internal/validator/validator';
import AWSDKError from '../../awsdk/error/awsdk_error';
import AWFRERecommendationsRequest from '../model/recommendations/awfre_recommendations_request';
import AWFRERecommendationsResponse from '../model/response/awfre_recommendations_response';
import AWFRERecommendationsAdditionalDataRequest from '../model/recommendations/awfre_recommendations_additional_data_request';

/**
 * This service handles everything related to FRE (Federated Recommendation Engine) calls
 *
 * @since 2.4.0
 * 
 * @hideconstructor
 * @extends service.Service
 */
class FreService extends Service {
  constructor(config) {
    super(config);
    this.__config = config;
  }

  /**
    * Method for getting recommendations (AmWell enterprise installations) from the FRE server.
    * @param {model.AWFRERecommendationsRequest} recommendationsRequest The recommendation request that contains consumer input for recommendations.
    * @returns {Promise<model.AWFRERecommendationsResponse|error.AWSDKError>} A Promise that resolves to an {@link model.AWFRERecommendationsResponse|AWFRERecommendationsResponse} or is rejected with a {@link error.AWSDKError|AWSDKError}
    * <p><br>Potential Error Codes<br>
    * <table summary="ErrorCodes" border="1">
    * <thead>
    * <tr><th>Error Code</th><th>reason</th></tr>
    * </thead>
    * <tbody>
    * <tr><td>{@link error.AWSDKErrorCode.illegalArgument|AWSDKErrorCode.illegalArgument}</td><td>A parameter is not the correct type.</td></tr>
    * <tr><td>{@link error.AWSDKErrorCode.internalError|AWSDKErrorCode.internalError}</td><td>The AWSDK could not complete the request.</td></tr>
    * <tr><td>{@link error.AWSDKErrorCode.validationError|AWSDKErrorCode.AWSDKFieldValidationError}</td><td>If the cfgId field is empty, or if the state has two uppercase letters.
    * </td></tr>
    * </tbody>
    * </table>
    * @since 2.4.0
    */
  getRecommendations(recommendationsRequest) {
    const currentFunction = 'FreService.getRecommendations';
    this.__logger.debug(currentFunction, 'Started', recommendationsRequest);

    if (!(recommendationsRequest instanceof AWFRERecommendationsRequest)) {
      const error = AWSDKError.AWSDKIllegalArgument('recommendationsRequest argument must be of type AWFRERecommendationsRequest');
      this.__logger.error(currentFunction, 'Error', error);
      return Promise.reject(error);
    }

    if (!Validator.isValidString(recommendationsRequest.cfgId)) {
      const error = AWSDKError.AWSDKFieldValidationError(null, 'cfgId', 'field required', 'set to non-empty value');
      this.__logger.error(currentFunction, 'error', error);
      return Promise.reject(error);
    }

    if (!Validator.isValidString(recommendationsRequest.state)) {
      const error = AWSDKError.AWSDKFieldValidationError(null, 'state', 'field required', 'set to non-empty value');
      this.__logger.error(currentFunction, 'error', error);
      return Promise.reject(error);
    }

    const link = this.findNamedLink(this.__links, 'recommendations');
    if (!Validator.isValidLink(link)) {
      const error = AWSDKError.AWSDKInternalError('service does not have a "recommendations" link entry');
      this.__logger.error(currentFunction, 'error', error);
      return Promise.reject(error);
    }

    const options = this.generateOptions('POST', link.url, false);
    options.headers['Content-Type'] = 'application/json';
    options.body = recommendationsRequest.__getAsRequestBody();

    return this.executeRequest(options, AWFRERecommendationsResponse)
      .then((recommendationsResponse) => {
        this.__logger.debug(currentFunction, 'Got response', recommendationsResponse);
        return Promise.resolve(recommendationsResponse);
      })
      .catch((error) => {
        this.__logger.error(currentFunction, 'Error', error);
        throw error;
      });
  }


  /**
    * Method for retrieving recommendations (AmWell enterprise installations) from the FRE server.
    * This method depends on trackingId coming from the getRecommendations method and may return additional data such as visit cost (if exists).
    * In case no additional data exists, it will return the same as getRecommendations.
    * @param {model.AWFRERecommendationsAdditionalDataRequest} recommendationsAdditionalDataRequest Request for getting additional data that was late to gathered, such as cost.
    * @returns {Promise<model.AWFRERecommendationsResponse|error.AWSDKError>} a Promise that resolves to an {@link model.AWFRERecommendationsResponse|AWFRERecommendationsResponse} or is rejected with a {@link error.AWSDKError|AWSDKError}
    * <p><br>Potential Error Codes<br>
    * <table summary="ErrorCodes" border="1">
    * <thead>
    * <tr><th>Error Code</th><th>reason</th></tr>
    * </thead>
    * <tbody>
    * <tr><td>{@link error.AWSDKErrorCode.illegalArgument|AWSDKErrorCode.illegalArgument}</td><td>A parameter is not the correct type.</td></tr>
    * <tr><td>{@link error.AWSDKErrorCode.internalError|AWSDKErrorCode.internalError}</td><td>The AWSDK could not complete the request.</td></tr>
    * <tr><td>{@link error.AWSDKErrorCode.validationError|AWSDKErrorCode.AWSDKFieldValidationError}</td><td>If the tracking field is empty.</td></tr>
    * </tbody>
    * </table>
    * 
    * @since 2.4.0
    */
   getRecommendationsAdditionalData(recommendationsAdditionalDataRequest) {
    const currentFunction = 'FreService.getRecommendationsAdditionalData';
    this.__logger.debug(currentFunction, 'Started', recommendationsAdditionalDataRequest);
    if (!(recommendationsAdditionalDataRequest instanceof AWFRERecommendationsAdditionalDataRequest)) {
      const error = AWSDKError.AWSDKIllegalArgument('recommendationsAdditionalDataRequest argument must be of type AWFRERecommendationsAdditionalDataRequest');
      this.__logger.error(currentFunction, 'Error', error);
      return Promise.reject(error);
    }

    if (!Validator.isValidString(recommendationsAdditionalDataRequest.trackingId)) {
      const error = AWSDKError.AWSDKFieldValidationError(null, 'trackingId', 'field required', 'set to non-empty value');
      this.__logger.error(currentFunction, 'error', error);
      return Promise.reject(error);
    }

    const link = this.findNamedLink(this.__links, 'recommendations-tracking');

    if (!Validator.isValidLink(link)) {
      const error = AWSDKError.AWSDKInternalError('service does not have a "recommendations-AdditionalData" link entry');
      this.__logger.error(currentFunction, 'error', error);
      return Promise.reject(error);
    }

    const options = this.generateOptions('POST', link.url, false);
    options.headers['Content-Type'] = 'application/json';
    options.body = recommendationsAdditionalDataRequest.__getAsRequestBody();
    return this.executeRequest(options, AWFRERecommendationsResponse)
      .then((recommendationsResponse) => {
        this.__logger.debug(currentFunction, 'Got response', recommendationsResponse);
        return Promise.resolve(recommendationsResponse);
      })
      .catch((error) => {
        this.__logger.error(currentFunction, 'Error', error);
        throw error;
      });
  }

  /**
   * This method calls getRecommendationsAdditionalData at every interval until timeout or error limit reached.
   * Polling functionality is required since some of the data (e.g. visit cost) may require more time to be retrieved. 
   * @param {model.AWFRERecommendationsAdditionalDataRequest} recommendationsAdditionalDataRequest is the object that was created by a call to {@link service.FreService#getRecommendationsAdditionalData}.
   * @param {function} [callback] the callback to be called on while the recommendations is in progress.
   * @returns {Promise<model.AWFRERecommendationsResponse|error.AWSDKError>} Returns a promise that will be resolved to an {@link model.AWFRERecommendationsResponse|AWFRERecommendationsResponse} or will
   * be rejected with an {@link error.AWSDKError|AWSDKError}.
   * <p><br>Potential Error Codes<br>
   * <table summary="ErrorCodes" border="1">
   * <tr><th>Error Code</th><th>reason</th>
   * <tr><td>{@link error.AWSDKErrorCode.illegalArgument|AWSDKErrorCode.illegalArgument}</td><td>A parameter is not the correct type.</td></tr>
   * </table>
   * @since 2.4.0
   */
  waitForRecommendationsAdditionalDataToFinish(recommendationsAdditionalDataRequest, callback) {
    const currentFunction = 'FreService.waitForRecommendationsAdditionalDataToFinish';
    this.__logger.debug(currentFunction, 'Started', recommendationsAdditionalDataRequest);

    if (callback != null && !(callback instanceof Function)) {
      const error = AWSDKError.AWSDKIllegalArgument('callback argument is not a function');
      this.__logger.error(currentFunction, 'error', error);
      return Promise.reject(error);
    }

    let errorCount = 0;
    const errorLimit = 3;
    const interval = this.__config.recommendationsPollingInterval; 
    const timeout = this.__config.recommendationsPollingTimeout;
    const endTime = Number(new Date()) + (timeout);

    const checkCondition = (resolve, reject) => {
      this.getRecommendationsAdditionalData(recommendationsAdditionalDataRequest)
        .then((response) => {
          if (Number(new Date()) < endTime) {
            if (callback != null) {
              try {
                 callback(response);
               } catch (error) {
                this.__logger.error(currentFunction, 'error', error);
              }
            }
            setTimeout(checkCondition, interval, resolve, reject);
          } else {
            resolve(response);
          }
        })
        .catch((error) => {
          errorCount += 1;
          this.__logger.warn(currentFunction, 'error during polling', errorCount, error);
          if (errorCount >= errorLimit) {
            this.__logger.error(currentFunction, 'Error', error);
            reject(error);
          } else {
            setTimeout(checkCondition, interval, resolve, reject);
          }
        });
    };
    return new Promise(checkCondition);
  }
}

export default FreService;
