import AWFREDataObject from '../awfre_dataobject';
import GenericParser from '../../../awsdk/internal/parser/generic_parser';
import AWSDKGender from '../../../awsdk//model/consumer/awsdk_gender';

/**
 * Demographics object holds the relevant consumer's demographics data.
 * 
 * @property {String} firstName The consumer first name.
 * @property {String} lastName The consumer last name.
 * @property {model.AWSDKGender} The consumer gender {@link model.AWSDKGender|AWSDKGender}
 * @property {Date} dateOfBirth The consumer date of birth.
 *
 * @since 2.4.0
 */
class AWFREDemographics extends AWFREDataObject {
  constructor(data) {
    super(data);
  }

  get firstName() {
    return this.__data.firstName;
  }

  get lastName() {
    return this.__data.lastName;
  }

  get gender() {
    return GenericParser.parseEnum(this.__data.gender, AWSDKGender);
  }

  get dateOfBirth() {
    return GenericParser.parseDate(this.__data.dateOfBirth);
  }

  set firstName(firstName) {
    this.__data.firstName = firstName;
  }

  set lastName(lastName) {
    this.__data.lastName = lastName;
  }

  set gender(gender) {
    this.__data.gender = gender;
  }

  set dateOfBirth(dateOfBirth) {
    this.__data.dateOfBirth = dateOfBirth;
  }
}

export default AWFREDemographics;
