import AWFREDataObject from '../awfre_dataobject';
import AWFREHealthPlan from '../healthplans/awfre_health_plan';
import AWFREPractice from './awfre_practice';
import GenericParser from '../../../awsdk/internal/parser/generic_parser';

/**
 * This object is used to hold a single recommendation in the recommendations response.
 * 
 * @property {String} installationId Unique identifier of the enterprise installation.
 * @property {String} name is the name of the enterprise.
 * @property {String[]} states The state where the enterprise is installed. For example CA (California).
 * @property {String} locale Locale. For example en_US.
 * @property {String} webSdkKey Web SDK key required to connect to an enterprise installation from a website.
 * @property {String} logoURL The URL of the enterprise logo.
 * @property {String} serverURL The URL of the enterprise installation server.
 * @property {model.AWFREHealthPlan[]} healthPlans A list of health plans covered by the enterprise.
 * @property {model.AWFREPractice[]} practices A list of practices offered by the enterprise.
 * @property {boolean} likelyCovered An indicator that services offered by the platform are likely to be covered according to the health plan supplied by the customer and the list of supported health plans configured on the enterprise.
 *
 * @since 2.4.0
 */
class AWFREEnterprise extends AWFREDataObject {
  get installationId() {
    return this.__data.installationId;
  }

  get name() {
    return this.__data.name;
  }

  get states() {
    return this.__data.states;
  }

  get locale() {
    return this.__data.locale;
  }

  get likelyCovered() {
    return GenericParser.parseBoolean(this.__data.likelyCovered);
  }

  get webSdkKey() {
    return this.__data.webSdkKey;
  }

  get logoURL() {
    return this.__data.logoURL;
  }

  get serverURL() {
    return this.__data.serverURL;
  }

  get healthPlans() {
    const healthPlans = this.__data.healthPlans;
    if (!Array.isArray(healthPlans) || healthPlans.length === 0) {
      return [];
    }
    return healthPlans.map(item => GenericParser.parseObject(item, AWFREHealthPlan));
  }

  get practices() {
    const practices = this.__data.practices;
    if (!Array.isArray(practices) || practices.length === 0) {
      return [];
    }
    return practices.map(item => GenericParser.parseObject(item, AWFREPractice));
  }
}

export default AWFREEnterprise;
