/**
 * Enum for defining sort order.
 * @readonly
 * @enum {string}
 * @deprecated
 */
const AWSDKSortOrder = {

  /**
   * Defines an ascending sort order
   * @since 1.0.0
   */
  ASC: 'asc',

  /**
   * Defines a descending sort order
   * @since 1.0.0
   */
  DESC: 'desc',
};
Object.freeze(AWSDKSortOrder);
export default AWSDKSortOrder;
