/**
 * This is the main entry point of the American Well AWFRE SDK
 */
import FreInitializationService from './internal/service/fre_initialization_service';
import Logger from '../awsdk/internal/logger/logger';
import AWFREError from './error/awfre_error';
import FreService from '../awfre/service/fre_service';
import log from 'loglevel';
import version from '../awsdk/version';

/**
 * This is the main JS file which is exposed as part of the awsdk.js
 * 
 * @since 2.4.0
 */
class AWFRE {
  constructor() {
    this.__logger = new Logger();
  }

/**
 * Required call to initialize the AWFRE.
 * Upon success, the AWFRE instance will be initialized and configured.
 * @param {Object} config The configuration used to initialize the FRE.
 * @param {String} config.freBaseUrl https://fre-american-well.com/ The base American Well telehealth platform URL to target.
 * @returns {Promise<boolean|error.AWSDKError>} Returns a promise that will be resolved to a boolean or will be rejected with an {@link error.AWFREError}.
 * 
 * <br>Potential Error Codes<br>
 * <table summary="ErrorCodes" border="1">
 * <tr><th>Error Code</th><th>reason</th></tr>
 * <tr><td>{@link error.AWFREInitializationError|AWFREError.AWFREInitializationError}</td><td>The FRE failed to initialize</td></tr>
 * </table>
 * 
 * @since 2.4.0
 */
  initializeFre(config = {}) {
    this.reset();

    this.__config = config;
    this.__config.logger = this.__logger;
    this.__logger.info('Initializing AWFRE');

    /**
     * always check for a trailing slash. All of our URL appending logic internally in the rest of the models depends on this.
     */
    if (this.__config.freBaseUrl.charAt(this.__config.freBaseUrl.length - 1) !== '/') {
      this.__config.freBaseUrl += '/';
    }

    this.__freInitializationService = new FreInitializationService(this.__config);

    if (!this.__freInitializationService.validate(this.__config)) {
      this.__initialized = false;
      return Promise.reject(AWFREError.AWFREInitializationError());
    }

    return (this.__freInitializationService.initialization(this.__config))
      .then((awfreInitializationResponse) => {
        this.__links = awfreInitializationResponse.links;
        this.__healthPlans = awfreInitializationResponse.healthPlans;
        this.__services = awfreInitializationResponse.services;
        this.__initialized = true;
        const versionLog = log.getLogger('AWSDKVersion');
        versionLog.enableAll();
        versionLog.info('AWSDK Version', version.sdkVersion);
        this.__logger.info('info', `AWFRE initialization success. Response: ${awfreInitializationResponse}`);
        return true;
      })
      .catch((error) => {
        this.__logger.error('AWFRE.initialize', 'Error', error);
        throw error;
      });
  }

  reset() {
    this.__initialized = false;
    this.__healthPlans = undefined;
    this.__services = undefined;
    this.__freService = undefined;
    this.__freInitializationService = undefined;
  }

  /**
   * Get a {@link service.FreService}
   * @type {service.FreService}
   */
  get freService() {
    if (!this.__initialized) {
      throw AWFREError.AWFREInitializationError();
    }
    if (this.__freService === undefined) {
      this.__freService = new FreService({
        logger: this.__logger, 
        links: this.__freInitializationService.__links, 
        recommendationsPollingInterval: this.__config.recommendationsPollingInterval, 
        recommendationsPollingTimeout: this.__config.recommendationsPollingTimeout 
      });
    }
    return this.__freService;
  }

  get services() {
    return this.__services;
  }

  get healthPlans() {
    return this.__healthPlans;
  }

  restoreInstanceState(state) {
    const newState = JSON.parse(state);
    this.__config = newState.config;
    this.__links = newState.links;
    this.__healthPlans = newState.healthPlans;
    this.__services = newState.services;
    this.__initialized = newState.initialized;

    this.__freInitializationService.setConfig(this.__config);
    this.__freInitializationService.setLinks(this.__links);
    this.__initialized = GenericParser.parseBoolean(this.__initialized);
  }

}

export default AWFRE;
