import AWFREDataObject from './awfre_dataobject';
/**
 * @class
 * @ignore
 * 
 */
class AWFRERestLink extends AWFREDataObject {
  constructor(data) {
    super(data);
  }

  get name() {
    return this.__data.name;
  }

  get url() {
    return this.__data.url;
  }

  get method() {
    return this.__data.method;
  }
}

export default AWFRERestLink;
