import AWFREResponse from './awfre_response';
import GenericParser from '../../../awsdk/internal/parser/generic_parser';
import AWFRERestLink from '../awfre_rest_link';
import AWFREHealthPlan from '../healthplans/awfre_health_plan';
import AWFREService from '../awfre_service';

/**
  * The AWFREInitializationResponse class represents the response object to an initialization call.
  * 
  * @property {model.AWFREHealthPlan[]} healthPlans The health plan details for a given cfgId.
  * @property {model.AWFREService[]} services The available health services.
  *
  * @since 2.4.0
  */
class AWFREInitializationResponse extends AWFREResponse {

  /**
   * @private
   */
  get links() {
    const links = this.__data.links;
    if (!Array.isArray(links) || links.length === 0) {
      return [];
    }
    return links.map(item => GenericParser.parseObject(item, AWFRERestLink));
  }

  get healthPlans() {
    const healthPlans = this.__data.healthPlans;
    if (!Array.isArray(healthPlans) || healthPlans.length === 0) {
      return [];
    }
    return healthPlans.map(item => GenericParser.parseObject(item, AWFREHealthPlan));
  }

  get services() {
    const services = this.__data.services;
    if (!Array.isArray(services) || services.length === 0) {
      return [];
    }
    return services.map(item => GenericParser.parseObject(item, AWFREService));
  }
}

export default AWFREInitializationResponse;
