import AWFREDataObject from '../awfre_dataobject';

/**
 * Visit Cost object that holds information about estimated visit costs.
 * 
 * @property {Number} costLow The lowest possible cost calculated per practice.
 * @property {Number} costHigh The highest possible cost calculated per practice.
 *
 * @since 2.4.0
 */
class AWFREVisitCost extends AWFREDataObject {

  get costLow() {
    return this.__data.costLow;
  }

  get costHigh() {
    return this.__data.costHigh;
  }
}

export default AWFREVisitCost;
