import AWFREErrorCode from './awfre_error_code';

/**
 * This class represents a wrapper class for any Error encountered by the AWFRE library during interaction with the server or internal to the AWFRE. <br>
 * Its purpose is to provide a more digestible data type for error encapsulation and handling.
 * 
 * @property {error.AWFREErrorCode} errorCode the {@link error.AWFREErrorCode|AWFREErrorCode} associated with this error object.
 * @property {String} message String message of the main error.
 * @property {String} reason String explanation for the cause of the error.
 * @property {String} recoverySuggestion String suggesting a possible recovery strategy, if applicable.
 * @property {Number} httpResponseCode Integer representing the http error code associated with the error.
 * 
 * @since 2.4.0
 */
class AWFREError {
  constructor(error, message, reason, recoverySuggestion, httpResponseCode) {
    if (error instanceof Error) {
      this.__errorCode = AWFREErrorCode.otherError;
      this.__error = error;
    } else if (typeof error === 'symbol' || AWFREErrorCode.__codes.includes(error)) {
      this.__errorCode = error;
      this.__error = new Error(message);
    } else {
      this.__errorCode = AWFREErrorCode.otherError;
      this.__error = new Error(message);
    }
    this.__reason = reason;
    this.__recoverySuggestion = recoverySuggestion;
    this.__httpResponseCode = httpResponseCode;
  }
  static AWFREInitializationError() {
    return new AWFREError(AWFREErrorCode.initializationError,
      'AWFRE was not properly initialized',
      'Potential incorrect or missing cfgId field or wrong server url',
      'Retry AWFRE initialization and ensure you receive a success');
  }
  static AWFREVersionIncompatibleError() {
    return new AWFREError(AWFREErrorCode.versionIncompatible,
      'The FRE platform is incompatible with this version of sdk',
      '',
      'Either upgrade the FRE platform or use different sdk version that is compatible');
  }
  get errorCode() {
    return this.__errorCode;
  }
  get message() {
    return this.__error.message;
  }
  get reason() {
    return this.__reason;
  }
  get recoverySuggestion() {
    return this.__recoverySuggestion;
  }
  get httpResponseCode() {
    return this.__httpResponseCode;
  }
  set httpResponseCode(httpResponseCode) {
    this.__httpResponseCode = httpResponseCode;
  }
}

export default AWFREError;
