import GenericParser from '../../../awsdk/internal/parser/generic_parser';
import AWFREEnterprise from '../recommendations/awfre_enterprise';
import AWFREResponse from './awfre_response';

/**
 * The AWFRERecommendationsResponse class represents the response object to a call for a recommendation details call.
 * @property {String} trackingId is a Unique identifier retrieved as a part of the recommendations response in order to get updated cost for recommendations.
 * @property {model.AWFREEnterprise[]} enterprises The enterprises details for a given cfgId.
 * @property {String} [requestId] Identifier of the request.
 *
 * @since 2.4.0
 */
class AWFRERecommendationsResponse extends AWFREResponse {
  get trackingId() {
    return this.__data.trackingId;
  }

  get enterprises() {
    const enterprises = this.__data.enterprises;
    if (!Array.isArray(enterprises) || enterprises.length === 0) {
      return [];
    }
    return enterprises.map(item => GenericParser.parseObject(item, AWFREEnterprise));
  }

  get requestId() {
    return this.__data.requestId;
  }
}

export default AWFRERecommendationsResponse;
