/**
 * A Service for initializing FRE connection to the server.
 * By connecting we're getting the REST links that we can call while we got this
 * server instantiated
 */
import Service from '../../../awsdk/service/service';
import Validator from '../../../awsdk/internal/validator/validator';
import AWFREInitializationResponse from '../../model/response/awfre_initialization_response';

/**
 * This service handles everything related to the initialization process of FRE (Federated Recommendation Engine)
 * @class
 * @ignore
 * 
 * @since 2.4.0
 * @hideconstructor
 * @extends service.Service
 */
class FreInitializationService extends Service {
  constructor(config) {
    super(config);
    this.__config = config;
  }

  initialization() {
    const initializationUrl = `${this.__config.freBaseUrl}fre/rest/ext/sdk/initialization`;
    const options = this.generateOptions('POST', initializationUrl, false);
    options.headers['Content-Type'] = 'application/json';
    const initializationBody = {
      cfgId: this.__config.cfgId,
      locale: 'en_US',
    };
    options.body = JSON.stringify(initializationBody);
    const currentFunction = 'initialization';
    return this.executeRequest(options, AWFREInitializationResponse)
      .then((initializationResponse) => {
        this.__logger.debug(currentFunction, 'Got initialization response', initializationResponse);
        this.__links = initializationResponse.links;
        this.__healthPlans = initializationResponse.healthPlans;
        this.__services = initializationResponse.services;
        return initializationResponse;
      })
      .catch((error) => {
        this.__logger.error(currentFunction, 'Error', error);
        throw error;
      });
  }

  get config() {
    return this.__config;
  }

  set config(config) {
    this.__config = config;
  }

  get links() {
    return this.__links;
  }

  set links(links) {
    this.__links = links;
  }

  validate(freConfig) {
    return Validator.isValidString(freConfig.cfgId) && Validator.isValidString(freConfig.freBaseUrl);
  }
}

export default FreInitializationService;
