import AWFREDataObject from '../awfre_dataobject';

/**
 * Health plan object to hold the information of enterprise's health plan.
 * 
 * @property {String} healthPlanName The health plan name.
 * @property {String} healthPlanQualifier The health plan unique identifier.
 * @property {String} healthPlanDisplayName The health plan display name, original name.
 *
 * @since 2.4.0
 */
class AWFREHealthPlan extends AWFREDataObject {
  constructor(data) {
    super(data);
  }

  get healthPlanName() {
    return this.__data.healthPlanName;
  }

  get healthPlanQualifier() {
    return this.__data.healthPlanQualifier;
  }

  get healthPlanDisplayName() {
    return this.__data.healthPlanDisplayName;
  }
}

export default AWFREHealthPlan;