import GenericParser from '../../../awsdk/internal/parser/generic_parser';
import AWFREDemographics from './awfre_demographics';

/**
 * Request object to hold the relevant fields for making Recommendations call.
 * 
 * @property {String} cfgId Consumer aggregator id.
 * @property {String} state The state where the enterprise is installed. For example CA (California).
 * @property {String} [requestId] Consumer Request id.
 * @property {String} [healthPlanQualifier] The health plan unique identifier.
 * @property {String} [serviceId] Service id is an identifier of Service type.
 * @property {String} [subscriberId] health plan subscriber id.
 * @property {String} [suffix] Suffix for the health plan subscriber id.
 * @property {model.AWFREDemographics} [demographics] Contains demographic information about the consumer.
 *
 * @since 2.4.0
 */
class AWFRERecommendationsRequest {
  get requestId() {
    return this.__requestId;
  }

  get cfgId() {
    return this.__cfgId;
  }

  get state() {
    return this.__state;
  }

  get healthPlanQualifier() {
    return this.__healthPlanQualifier;
  }

  get serviceId() {
    return this.__serviceId;
  }

  get subscriberId() {
    return this.__subscriberId;
  }

  get suffix() {
    return this.__suffix;
  }

  get demographics() {
    return GenericParser.parseObject(this.__demographics, AWFREDemographics);
  }

  set requestId(requestId) {
    this.__requestId = requestId;
  }

  set cfgId(cfgId) {
    this.__cfgId = cfgId;
  }

  set state(state) {
    this.__state = state;
  }

  set healthPlanQualifier(healthPlanQualifier) {
    this.__healthPlanQualifier = healthPlanQualifier;
  }

  set serviceId(serviceId) {
    this.__serviceId = serviceId;
  }

  set subscriberId(subscriberId) {
    this.__subscriberId = subscriberId;
  }

  set suffix(suffix) {
    this.__suffix = suffix;
  }

  set demographics(demographics) {
    this.__demographics = demographics;
  }

  /**
   * @private
   */
  __getAsRequestBody() {
    const res = {};

    res.cfgId = this.__cfgId;
    res.requestId = this.__requestId;
    res.state = this.__state;
    res.healthPlanQualifier = this.__healthPlanQualifier;
    res.serviceId = this.__serviceId;
    res.subscriberId = this.__subscriberId;
    res.suffix = this.__suffix;
    res.demographics = this.__demographics;

    return JSON.stringify(res);
  }
}
export default AWFRERecommendationsRequest;
